<template>
    <div class="fight">
        <div class="toRole">
            <p>{{ $t("toRole") }}</p>
            <a-select
                v-model:value="enemyId"
                show-search
                placeholder="Select a person"
                :options="roleOptionsList"
                :filter-option="filterOption"
                @focus="handleFocus"
                @blur="handleBlur"
                @change="handleChange"
            ></a-select>
            <a-button type="primary" @click="toRole" :disabled="!enemyId">
                {{ $t("attack") }}
            </a-button>
        </div>
        <div class="toMonster">
            <p>{{ $t("toMonster") }}</p>
            <a-button type="primary" @click="toMonster">
                {{ $t("attack") }}
            </a-button>
            <a-button type="primary" @click="toImitMonster">
                {{ $t("imitAttack") }}
            </a-button>
            <div>
                {{ $t("currentWinRate") }} {{ currentMonsterWinRate }}
                <a-tooltip>
                    <template #title>{{ $t("monsterTips") }}</template>
                    <QuestionCircleOutlined style="color: #d2d2d2" />
                </a-tooltip>
            </div>
        </div>
        <div class="toBoss">
            <p>{{ $t("toBoss") }}</p>
            <a-button type="primary" @click="toBoss">
                {{ $t("attack") }}
            </a-button>
            <a-button type="primary" @click="toImitBoss">
                {{ $t("imitAttack") }}
            </a-button>
            <div>
                {{ $t("currentWinRate") }} {{ currentBossWinRate }}
                <a-tooltip>
                    <template #title>{{ $t("bossTips") }}</template>
                    <QuestionCircleOutlined style="color: #d2d2d2" />
                </a-tooltip>
            </div>
        </div>
    </div>
    <FightModal
        :modalVisible="modalVisible"
        :fightLog="fightLog"
        @submit="modalClose"
    ></FightModal>
</template>

<script>
import { reactive, toRefs, onMounted, ref, createVNode, computed } from "vue";
import fightApi from "@/assets/api/fightApi";
import FightModal from "@/components/FightModal.vue";
import { Modal } from "ant-design-vue";
import { QuestionCircleOutlined } from "@ant-design/icons-vue";
import roleApi from "@/assets/api/roleApi";

export default {
    name: "Fight",
    components: { FightModal, QuestionCircleOutlined },
    // 战斗
    setup() {
        const state = reactive({
            enemyName: "",
            enemyId: "",
            // 人员列表
            roleOptionsList: [
                { value: "jack", label: "Jack" },
                { value: "lucy", label: "Lucy" },
                { value: "tom", label: "Tom" },
            ],

            fightLog: [], // 战斗日志
            currentMonsterWinRate: "0%", // monster 胜率
            currentBossWinRate: "0%", // boss 胜率
            modalVisible: false, // 战斗日志弹窗
        });

        onMounted(async () => {
            // 获取所有角色列表
            await getRoleList();
            await toImitMonster();
            await toImitBoss();
        });

        // 获取所有角色列表
        const getRoleList = async () => {
            let res = await roleApi.getRoleList({});
            state.roleOptionsList = res.data.map((i) => {
                return {
                    label: i.nickname,
                    value: i.uid,
                };
            });
        };

        // attack to role
        const toRole = async () => {
            //  二次确认
            Modal.confirm({
                title: `Do you Want to attack for ${state.enemyName}?`,
                // icon: createVNode(ExclamationCircleOutlined),
                content: createVNode(
                    "div",
                    { style: "color:red;" }
                    // "Some descriptions"
                ),
                async onOk() {
                    let res = await fightApi.toRole({
                        uid: sessionStorage.getItem("uid"),
                        enemyId: String(state.enemyId),
                    });
                    state.fightLog = res.data.fightLog;
                    state.modalVisible = true;
                },
                onCancel() {
                    console.log("Cancel");
                },
                class: "test",
            });
        };

        const toMonster = async () => {
            //  二次确认
            Modal.confirm({
                title: `Do you Want to attack for Monster?`,
                // icon: createVNode(ExclamationCircleOutlined),
                content: createVNode(
                    "div",
                    { style: "color:red;" }
                    // "Some descriptions"
                ),
                async onOk() {
                    let res = await fightApi.toMonster({
                        uid: sessionStorage.getItem("uid"),
                    });
                    state.fightLog = res.data.fightLog;
                    state.modalVisible = true;
                },
                onCancel() {
                    console.log("Cancel");
                },
                class: "test",
            });
        };

        const toBoss = async () => {
            //  二次确认
            Modal.confirm({
                title: `Do you Want to attack for Boss?`,
                // icon: createVNode(ExclamationCircleOutlined),
                content: createVNode(
                    "div",
                    { style: "color:red;" }
                    // "Some descriptions"
                ),
                async onOk() {
                    let res = await fightApi.toBoss({
                        uid: sessionStorage.getItem("uid"),
                    });
                    state.fightLog = res.data.fightLog;
                    state.modalVisible = true;
                },
                onCancel() {
                    console.log("Cancel");
                },
                class: "test",
            });
        };

        const toImitMonster = async () => {
            let res = await fightApi.toImitMonster({
                uid: sessionStorage.getItem("uid"),
            });
            let roleWin = 0;
            for (let i = 0; i < res.data.fightLogs.length; i++) {
                if (res.data.fightLogs[i].sub === "admin") {
                    roleWin++;
                }
            }
            state.currentMonsterWinRate = roleWin + "%";
        };

        const toImitBoss = async () => {
            let res = await fightApi.toImitBoss({
                uid: sessionStorage.getItem("uid"),
            });
            let roleWin = 0;
            for (let i = 0; i < res.data.fightLogs.length; i++) {
                if (res.data.fightLogs[i].sub === "admin") {
                    roleWin++;
                }
            }
            state.currentBossWinRate = roleWin + "%";
        };

        const handleChange = (value) => {
            // 记录选中对手的nickname
            state.enemyName = state.roleOptionsList.find(
                (i) => i.value === value
            ).label;
        };
        const handleBlur = () => {
            // console.log("blur");
        };
        const handleFocus = () => {
            // console.log("focus");
        };

        // 过滤 人员列表
        const filterOption = (input, option) => {
            return option.value.toLowerCase().indexOf(input.toLowerCase()) >= 0;
        };

        // 关闭弹窗
        const modalClose = () => {
            state.modalVisible = false;
        };

        return {
            ...toRefs(state),
            filterOption,
            handleChange,
            handleBlur,
            handleFocus,
            QuestionCircleOutlined,
            toRole,
            toMonster,
            toImitMonster,
            toImitBoss,
            toBoss,
            modalClose,
        };
    },
};
</script>

<style lang="scss" scoped>
.fight {
    .toRole,
    .toMonster,
    .toBoss {
        float: left;
        border: 1px solid #d2d2d2;
        border-radius: 8px;
        margin: 0 10px 10px;
        padding: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .ant-select {
            width: 200px;
            margin-bottom: 20px;
        }

        .ant-btn {
            width: 100%;
            margin-bottom: 20px;
        }
    }
}
</style>
